@import url("http://fonts.googleapis.com/css?family=Roboto+Slab:100,300,400,700");
@import url("http://fonts.googleapis.com/css?family=Raleway:300,300i,400,400i,500,500i,600,600i");
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  /* background-color: #fff; */
}
  .wrapper{
    background: url('images/images.jpg');
    height: 85vh;
    text-align: center;
  }
  form{
    margin-top: 1px;
  }
  input{
    background: transparent;
    font-size: medium;
    color: #FFF;
    top: 40vh;
     bottom: 40vh;
    position: relative;
    border: 1px solid #FFF;
    display: inline-block;
    
    padding: 12px;
  }
  textarea{
    background: transparent;
    color: #FFF;
    font-size: large;
    top: 40vh;
     bottom: 40vh;
    position: relative;
    border: 1px solid #FFF;
    display: inline-block;
    padding: 12px;
    width: 297px;
  }
  button{
    background: transparent;
    font-size: medium;
    color: #FFF;
    top: 40vh;
     bottom: 40vh;
    position: relative;
    border: 1px solid #FFF;
    display: inline-block;
    padding: 8px;
  }
  .m{
      display: flex;
      justify-content:center;
      margin: 8px;
  }
  .note{
    background: #fff;
    border-radius: 7px;
    box-shadow: 0 2px 5px #ccc;
    padding: 10px;
    width:50%;
    margin:16px;
   
 
   
  }
  
  .note p{
    font-size:1.1em;
    margin-left: 54px;
    margin-bottom: 10px;
    white-space: pre-wrap;
    word-wrap: break-word;
  
  }
  
  .note button{
    
    position: inherit;
      font-family: "Raleway", sans-serif;
      text-transform: uppercase;
      height: 36px;
      width: 108px;
      color: #e45858;
      
      background: transparent;
      outline: none;
      border: none;
      cursor: pointer;
      border-radius: 10px;
      float: right;
      
      
  }
  .note button:hover{
    background-color: #c7c2c2;
  }
  .header img {
    float: left;
    width: 40px;
    height: 40px;
  }
  
  .header h1 {
    position: relative;
    margin-bottom: 6px;
    font-size:1.1em;
    top: 2px;
    left: 14px;
  }
  @media (max-width:768px){
    .note{
        background: #fff;
        border-radius: 7px;
        box-shadow: 0 2px 5px #ccc;
        padding: 10px;
        width:320px;
        margin:16px;
       
  }
 
}